/* :not(:required) hides this rule from IE9 and below */
.pong-loader:not(:required) {
  width: 80px;
  height: 60px;
  background: #353c39;
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-indent: 100%;
  -moz-box-shadow: inset #353c39 0 0 0 0, inset #353c39 0 -40px 0 0, inset #353c39 -3px 0 0 0, inset #8cdb8b -10px 0 0 0;
  -webkit-box-shadow: inset #353c39 0 0 0 0, inset #353c39 0 -40px 0 0, inset #353c39 -3px 0 0 0, inset #8cdb8b -10px 0 0 0;
  box-shadow: inset #353c39 0 0 0 0, inset #353c39 0 -40px 0 0, inset #353c39 -3px 0 0 0, inset #8cdb8b -10px 0 0 0;
  -moz-animation: pong-loader-paddle-1 1500ms infinite linear;
  -webkit-animation: pong-loader-paddle-1 1500ms infinite linear;
  animation: pong-loader-paddle-1 1500ms infinite linear;
}

.pong-loader:not(:required):before {
  -moz-animation: pong-loader-paddle-2 1500ms infinite linear;
  -webkit-animation: pong-loader-paddle-2 1500ms infinite linear;
  animation: pong-loader-paddle-2 1500ms infinite linear;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: "";
  width: 10px;
  height: 100%;
  -moz-box-shadow: inset #353c39 0 0 0 0, inset #353c39 0 -40px 0 0, inset #353c39 -3px 0 0 0, inset #8cdb8b -10px 0 0 0;
  -webkit-box-shadow: inset #353c39 0 0 0 0, inset #353c39 0 -40px 0 0, inset #353c39 -3px 0 0 0, inset #8cdb8b -10px 0 0 0;
  box-shadow: inset #353c39 0 0 0 0, inset #353c39 0 -40px 0 0, inset #353c39 -3px 0 0 0, inset #8cdb8b -10px 0 0 0;
}

.pong-loader:not(:required):after {
  content: "";
  display: block;
  -moz-animation: pong-loader 1500ms infinite linear;
  -webkit-animation: pong-loader 1500ms infinite linear;
  animation: pong-loader 1500ms infinite linear;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 0;
  width: 10px;
  height: 10px;
  background: #8cdb8b;
}

.pulse-loader:not(:required) {
  display: inline-block;
  width: 50px;
  height: 50px;
  -moz-animation: pulse-loader 0.7s linear infinite alternate;
  -webkit-animation: pulse-loader 0.7s linear infinite alternate;
  animation: pulse-loader 0.7s linear infinite alternate;
  border: 2px solid #e67e22;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  overflow: hidden;
  text-indent: 50px;
}

@-moz-keyframes pulse-loader {
  0% {
    -moz-box-shadow: #e67e22 0 0 0px 20px;
    box-shadow: #e67e22 0 0 0px 20px;
  }
  40% {
    -moz-box-shadow: none;
    box-shadow: none;
  }
  100% {
    -moz-box-shadow: #e67e22 0 0 0px 25px inset;
    box-shadow: #e67e22 0 0 0px 25px inset;
  }
}

@-webkit-keyframes pulse-loader {
  0% {
    -webkit-box-shadow: #e67e22 0 0 0px 20px;
    box-shadow: #e67e22 0 0 0px 20px;
  }
  40% {
    -webkit-box-shadow: none;
    box-shadow: none;
  }
  100% {
    -webkit-box-shadow: #e67e22 0 0 0px 25px inset;
    box-shadow: #e67e22 0 0 0px 25px inset;
  }
}

@keyframes pulse-loader {
  0% {
    -moz-box-shadow: #e67e22 0 0 0px 20px;
    -webkit-box-shadow: #e67e22 0 0 0px 20px;
    box-shadow: #e67e22 0 0 0px 20px;
  }
  40% {
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
  }
  100% {
    -moz-box-shadow: #e67e22 0 0 0px 25px inset;
    -webkit-box-shadow: #e67e22 0 0 0px 25px inset;
    box-shadow: #e67e22 0 0 0px 25px inset;
  }
}

/* Thick cycle ----------------------------------*/
.loader-thick-cycle,
.loader-thick-cycle:after {
  border-radius: 50%;
  width: 5em;
  height: 5em;
}

.loader-thick-cycle {
  position: absolute;
  font-size: 10px;
  text-indent: -9999em;
  border-top: 0.7em solid rgba(255, 255, 255, 0.2);
  border-right: 0.7em solid rgba(255, 255, 255, 0.2);
  border-bottom: 0.7em solid rgba(255, 255, 255, 0.2);
  border-left: 0.7em solid #ffffff;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 0.5s infinite linear;
  animation: load8 0.5s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

/* Three bars ----------------------------------*/
.loader-three-bars,
.loader-three-bars:before,
.loader-three-bars:after {
  background: #ffffff;
  -webkit-animation: load1 0.5s infinite ease-in-out;
  animation: load1 0.5s infinite ease-in-out;
  width: 1em;
  height: 4em;
}

.loader-three-bars {
  position: absolute;
  top: calc(50% - 3.5px);
  left: calc(50% - 3.5px);
  color: #ffffff;
  text-indent: -9999em;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

.loader-three-bars:before,
.loader-three-bars:after {
  position: absolute;
  top: 0;
  content: '';
}

.loader-three-bars:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.loader-three-bars:after {
  left: 1.5em;
}

@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

/* Half Cycle ----------------------------------*/
.loader-half-cycle {
  position: absolute;
  top: calc(50% - 3.5px);
  left: calc(50% - 3.5px);
  font-size: 10px;
  text-indent: -9999em;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  background: #ffffff;
  background: -moz-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: -webkit-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: -o-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: -ms-linear-gradient(left, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  background: linear-gradient(to right, #ffffff 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  -webkit-animation: load3 0.5s infinite linear;
  animation: load3 0.5s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}

.loader-half-cycle:before {
  width: 50%;
  height: 50%;
  background: #ffffff;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}

.loader-half-cycle:after {
  background: #3a3f4f;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes downToUp {
  0% {
    margin-top: 20px;
    opacity: 0;
  }
  100% {
    margin-top: 0;
  }
}

@-webkit-keyframes downToUp {
  0% {
    margin-top: 20px;
    opacity: 0;
  }
  100% {
    margin-top: 0;
  }
}

